.p-pc-menu {
  display: none;

  @include mq(tab) {
     display: block;
  }
}

.p-pc-menu__items {
  display: flex;
  height: inherit;
  align-items: center;

}

.p-pc-menu__item {
height: rem(35);

}

.p-pc-menu__item a {
  letter-spacing: 0.125em;
  padding: rem(15);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  text-decoration: none;
  

}

.p-pc-menu__item a::after {
  position: absolute;
  bottom: 0;
  left: rem(15);
  content: '';
  width: calc(100% - #{rem(30)});
  height: 1px;
  background: $category-color;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: transform .3s;
}
.p-pc-menu__item a:hover::after {
  transform: scale(1, 1);
}
.p-pc-menu__btn {
  height: inherit;
  margin-left: rem(17);
  letter-spacing: 0.125em;
  display: none;

  @include mq(tab) {
    display: block;
  }


  .p-pc-menu__btn-in {
    padding: 0 rem(31);
  }

}